      "description": ":::info Why Pipeline? 团队内前辈们的选择Pipeline的优势, 可以让VSeed独立控制每一个图表类型的执行流程, 通过良好的设计, 让每个图表类型的实现解耦的同时又可以局部复用, 每一类图表类型都可以对任何细节的进行完美掌控, 这是 Pipeline 带来的, 也是VSeed最需要的.与之比起来, Pipeline模式的缺点都是可以在设计时避免的, 只要在设计Pipe时, 降低单个Pipe的规模, 减少Pipe之间的依赖, 就可以极大的避免这套模式带来的缺点经过四代的Pipeline的设计与优化, 到VSeed这里已经是第五个版本, 该踩的坑已经踩过了. :::"
