      "_relativePath": "zh-CN/vseed/option/histogram.md",
      "description": ":::info{title=编码映射}\n直方图支持以下视觉通道: xAxis  : x轴通道, 支持一个维度, 按维度值分箱计算后显示到x轴 ::: :::note{title=描述}\n直方图，适用于展示数据分布情况的场景，X轴为数值轴（连续数据），Y轴为数值轴（连续数据），柱子纵向排列 适用场景: - 展示数据的分布情况，如频率分布、概率分布等 - 分析数据的集中趋势和离散程度 - 识别数据中的异常值和模式 :::"
